 //    } from 'vuex';  
	let startY = 0, moveY = 0, pageAtTop = true;
    export default {
		components: {
			listCell
		},
		data(){
			return {
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				moving: false,
				userInfo :{
					user_id : 0,
					nickName : '',
					avatarUrl : ''
				},
			}
		},
		onLoad:function(){
			_self = this;
			loginRes = _self.checkLogin('../user/user',1); //1 跳回时用tabBar  2跳回时用reDriect
			if(!loginRes) { return false; }
			_self.loadData();
		},
		onShow() {
			//console.log('显示进入用户页面',uni.getStorage('SFACE'));

			//console.log(uni.getStorage('SFACE'));
		},




















        computed: {
			// ...mapState(['hasLogin','userInfo'])
		},
        methods: {
			test(){
				//_self.$api.msg('测试'); 
				//_self.$api.msg('数据里的'+_self.userInfo.nickName); 
				// let aaa = uni.getStorage('SNAME').then=>{
				// 	_self.$api.msg('后台的数据：'+aaa) 
				// };
				//let myv = uni.getStorageSync('SRAND');
				//_self.$api.msg(myv);
				
				// this.userInfo.user_id = iuser.data.user_id;
				// this.userInfo.user_name = iuser.data.user_name;
				// this.userInfo.nickName = iuser.data.nickName;
				// this.userInfo.avatarUrl = iuser.data.avatarUrl;
				_self.$api.msg(this.userInfo.nickName);
			},
			//加载用户信息
			async loadData(){
				let iuser = await _self.$apiRequest({
				 	url:'me'
				});
				//console.log(iuser);
				setTimeout(()=>{
					this.userInfo.user_id = iuser.data.user_id;
					this.userInfo.nickName = iuser.data.nickName;
					this.userInfo.avatarUrl = iuser.data.avatarUrl;
					//this.$api.msg('数据存取成功');
				}, 1000);
				
				//this.$api.msg('应该有数据了'+ uni.getStorageSync('SNAME'));
			},
			/**
			 * 统一跳转接口,拦截未登录路由
			 * navigator标签现在默认没有转场动画，所以用view
			 */
			navTo(url){
				// if(!this.hasLogin){
				// 	url = '/pages/public/login';
				// }
				uni.navigateTo({  
					url
				});
			}, 
	
			/**
			 *  会员卡下拉和回弹
			 *  1.关闭bounce避免ios端下拉冲突
			 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
			 *    transition设置0.1秒延迟，让css来过渡这段空窗期
			 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
			 */
			coverTouchstart(e){
				if(pageAtTop === false){
					return;
				}
				this.coverTransition = 'transform .1s linear';
				startY = e.touches[0].clientY;
			},
			coverTouchmove(e){
				moveY = e.touches[0].clientY;
				let moveDistance = moveY - startY;
				if(moveDistance < 0){
					this.moving = false;
					return;
				}
				this.moving = true;
				if(moveDistance >= 80 && moveDistance < 100){
					moveDistance = 80;
				}
		
				if(moveDistance > 0 && moveDistance <= 80){
					this.coverTransform = `translateY(${moveDistance}px)`;
				}
			},
			coverTouchend(){
				if(this.moving === false){
					return;
				}
				this.moving = false;
				this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
				this.coverTransform = 'translateY(0px)';
			}
        }  
    }  
